import React, { Component } from 'react';
import './dashboard.css';
//import {Apps} from '../data/util/dashboard';

export const Apps = [
    {
        id: "card 1",
        title: "App1",        
        image: "https://i.redd.it/b3esnz5ra34y.jpg",
        path: "/path",
    },
    {
        id: "card 2",
        title: "App2",        
        image: "https://source.unsplash.com/user/erondu/600x400",
        path: "/path2",
    },
    {
        id: "card 3",
        title: "App3",        
        image: "https://media.giphy.com/media/10SvWCbt1ytWCc/giphy.gif",
        path: "/path2",
    },
    {
        id: "card 4",
        title: "App3",        
        image: "https://media.giphy.com/media/LwIyvaNcnzsD6/giphy.gif",
        path: "/path2",
    },

    
];
class Main extends Component {
    constructor() {
        super();

        this.state = {
            apps: {}
        };
    }
    componentWillMount() {
        this.setState({
            apps: Apps
        });
    }

    render() {
        return (
            <div>
                <div class="cards-list">
                    {Object.keys(this.state.apps).map((key) => (
                        <Card key={key} index={key} details={this.state.apps[key]} />
                    ))}
                </div>
            </div>

        );
    }
}
class Card extends React.Component {
    render() {
      return (
        <div className={this.props.details.id} >
        <div class="card_image"> <img src={this.props.details.image} alt=""/> </div>
        <div class="card_title title-white">
            <p>{this.props.details.title}</p>
        </div>
    </div>
      )
    }
  }


export default Main;